import { Component, OnInit, Input } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { Store } from "@ngrx/store";
import { FormGroup, FormControl, Validators } from "@angular/forms";

import { DiagramService } from "../../services/diagram.service";
import { AddDiagramApiModel } from "../../models";
import * as FeatureReducer from "../../reducers";
import { AddDiagramAction } from "../../actions";

declare var _: any;

@Component({
	selector: "data-mapper",
	template: `<div fxLayout="row">
    <div fxFlex="grow">
        <b>
            Key
        </b>
    </div>
    <div fxFlex="nogrow">
        <b>
            Type
        </b>
    </div>
</div>
<div *ngFor="let item of dataReport" [class]="'depts_' + item.depts" fxLayout="row">
    <div fxFlex="grow">
        {{item.name}}
    </div>
    <div fxFlex="nogrow">
        {{item.type}}
    </div>
</div>
`,
	styles: [`mat-radio-button{width:100%;display:block}div.depts_2{margin-right:25px}div.depts_3{margin-right:50px}`]
})
export class DataMapperComponent implements OnInit {
	@Input() destination: string;
	_data: any;
	@Input()
	set data(data) {
		if (!data) return;
		this.dataReport = _.report(data);
		this._data = data;
	}
	dataReport: any[] = [];
	constructor(private diagramService: DiagramService, private store: Store<FeatureReducer.FeatureState>) {}

	ngOnInit() {}
}
